body {
    font: 14px/20px "Helvetica", "Century Gothic", Futura, sans-serif;
    margin: 60px 20px;
    text-align:center;
    color:#ffffff;
    background:#000000;
}

h2 {
    background-color:#787878;
    font-weight:normal;
    font-size:18px;
    margin:-5px auto 12px auto;
    padding:4px 10px;
    border-radius:0 0 5px 5px;
    display:inline-block;
    color:#000000;
}

hr {
    background:#787878;
    border:none;
    height:1px;
}

ul {
    list-style:none;
    padding:0;
}



